@charset "UTF-8";
* {
  font-family: 'Nanum Square', "Microsoft YaHei", "PingFang SC", "MS PGothic", "Hiragino Kaku Gothic ProN", "굴림", gulim, "Apple SD Gothic Neo", sans-serif;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
th,
td,
form,
fieldset,
legend,
input,
textarea,
img,
button,
select {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #fff;
  -webkit-text-size-adjust: none;
}

img,
fieldset {
  border: 0;
  vertical-align: top;
}

em,
address {
  font-style: normal;
}

ul,
ol,
li {
  list-style: none;
}

textarea {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  background-color: transparent;
  border: 0;
  word-break: keep-all;
  word-wrap: break-word;
}

button {
  appearance: none;
  -webkit-appearance: none;
  background-color: transparent;
  border: 0;
}

.blind {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

button:focus,
button:active {
  outline: none;
}

body.type_join {
  min-width: auto;
  background-color: #fff;
}

@media (min-width: 768px) and (max-width: 1023px) {
  body.type_join {
    background-color: #f5f7fa;
  }
}

@media screen and (min-width: 1023px) {
