}

.join .container {
  position: relative;
  height: calc(100vh - 54px);
}

@media (min-width: 667px) and (orientation: landscape) {
  .join .container {
    height: 100%;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .join .container {
    height: calc(100vh - 54px);
  }
}

@media screen and (min-width: 1024px) {
  .join .container {
    height: calc(100vh - 54px);
  }
}

.join .container.fix {
  width: 640px;
  height: auto;
  margin: 0 auto;
}

